'use client';
import axios from "axios";
import React, { useState, useEffect } from "react";
import { useRouter } from 'next/navigation';

const fetchClasses = async () => {
  try {
    const response = await axios.get("http://localhost:9921/classes");
    console.log("Fetched classes:", response.data);
    return response.data;
  } catch (error) {
    console.error("Error fetching classes:", error);
    return [];
  }
};

const AddClasses = () => {
  const [classes, setClasses] = useState([]);
  const [filteredClasses, setFilteredClasses] = useState([]);
  const [loading, setLoading] = useState(true);
  const [activeCategory, setActiveCategory] = useState("All Classes");
  const router = useRouter();

  useEffect(() => {
    const getClasses = async () => {
      const data = await fetchClasses();
      setClasses(data);
      setFilteredClasses(data);
      setLoading(false);
    };
    getClasses();
  }, []);

  const handleCategoryClick = (category) => {
    setActiveCategory(category);
    if (category === "All Classes") {
      setFilteredClasses(classes);
    } else {
      const filtered = classes.filter((classDetail) => classDetail.size === category);
      setFilteredClasses(filtered);
    }
  };

  const handleDeleteClass = async (classId) => {
    try {
      await axios.delete(`http://localhost:9921/class/${classId}`);
      const updatedClasses = classes.filter((classDetail) => classDetail.classId !== classId);
      setClasses(updatedClasses);
      setFilteredClasses(updatedClasses);
    } catch (error) {
      console.error("Error deleting class:", error);
    }
  };

  const handleEditClass = (classId) => {
    router.push(`/class_details/edit/${classId}`);
  };

  return (
    <div className="p-4 sm:p-6">
      <div className="mb-6 flex flex-col sm:flex-row justify-between items-center">
        <div>
          <h3 className="text-lg text-green-900 font-semibold">Class Management</h3>
          <p className="text-sm text-gray-500">Manage and monitor all available classes</p>
        </div>
        <button
          onClick={() => router.push('/class_details/add')}
          className="mt-2 sm:mt-0 px-4 py-2 bg-indigo-600 text-white rounded-md text-sm font-medium hover:bg-indigo-700"
        >
          Add New Classes
        </button>
      </div>

      <div className="bg-white rounded-lg shadow-md overflow-hidden">
        <div className="p-4 border-b flex flex-wrap gap-2 sm:gap-4">
          {["All Classes"].map((category) => (
            <button
              key={category}
              className={`px-3 py-1 rounded-md text-sm font-medium ${
                activeCategory === category
                  ? "bg-indigo-100 text-indigo-600"
                  : "bg-gray-100 text-gray-600 hover:bg-gray-200"
              }`}
              onClick={() => handleCategoryClick(category)}
            >
              {category}
            </button>
          ))}
        </div>

        {loading ? (
          <div className="p-4 text-center text-gray-500">Loading classes...</div>
        ) : (
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  {['Class ID', 'Class Code', 'Class Size', 'Year', 'Semester','Program ID', 'Actions'].map((heading) => (
                    <th key={heading} className="px-4 sm:px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      {heading}
                    </th>
                  ))}
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {filteredClasses.length > 0 ? (
                  filteredClasses.map((classDetail) => (
                    <tr key={classDetail.classId}>
                      <td className="px-4 sm:px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                        {classDetail.classId}
                      </td>
                      <td className="px-4 sm:px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {classDetail.classCode}
                      </td>
                      <td className="px-4 sm:px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {classDetail.size}
                      </td>
                      <td className="px-4 sm:px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {classDetail.yearId}
                      </td>
                      <td className="px-4 sm:px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {classDetail.semesterId}
                      </td>
                      <td className="px-4 sm:px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {classDetail.programId}
                      </td>
                      <td className="px-4 sm:px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        <div className="flex flex-col sm:flex-row gap-2">
                          <button className="text-indigo-600 hover:text-indigo-900" onClick={() => handleEditClass(classDetail.classId)}>Edit</button>
                          <button className="text-red-600 hover:text-red-900" onClick={() => handleDeleteClass(classDetail.classId)}>Delete</button>
                        </div>
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan="6" className="px-4 sm:px-6 py-4 text-center text-gray-500">
                      No classes found.
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
};

export default AddClasses;
